body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display: flex;
  width: 100%;
  height: auto;
  min-height: 100vh;
}

#root > nav {
  /* block */
  width: 42px;
  height: inherit;
  padding: 7px;
  overflow: hidden;
  white-space: nowrap;

  /* appearance */
  background-color: black;
  color: white;
  font-size: 22px;

  /* animation */
  transition: 300ms;
}

#root > nav:hover, #root > nav:focus {
  outline: none;    /* don't show when focused */
  width: 250px;
}

#root > nav > ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

#root > nav > ul > li {
    margin-bottom: 10px;
}

#root > nav > ul > li > a, #root > nav > ul > li > a:visited {
  display: flex;
  align-items: center;
  color: inherit;
  text-decoration: none;
}

#root > main {
  flex-grow: 1;
  padding: 4px 8px;
}
